<template>
  <div>
    <van-nav-bar title="分类" left-arrow @click-right="$router.go(-1)" />

    <van-sidebar v-model="activeKey">
      <div class="left">
        <van-sidebar-item
        v-for="item in firstCate"
        :key="item.id"
        :title="item.catename"
        @click="changeIndex"
      />
      </div>

      <van-grid :border="false" >
        <van-grid-item v-for="item in secondCate" :key="item.id" :to="'/goodslist?fid='+item.id" >
          <van-image :src="$preImg+item.img" />
        </van-grid-item>
      </van-grid>


<!-- v-if="firstCate[activeKey].children" -->
      <!-- <van-grid :border="false" v-else>

        <van-empty
  class="custom-image"
  image="https://img.yzcdn.cn/vant/custom-empty-image.png"
  description="暂无数据"
/>
      </van-grid> -->
    </van-sidebar>
  </div>
</template>

<script>
import { cart } from "../util/request";
export default {
  data() {
    return {
      activeKey: 0,
      //一级分类
      firstCate: [],
      //二级分类
      secondCate:[]
    };
  },
  methods:{
    changeIndex(i){
      this.secondCate=this.firstCate[i].children
    },
  },
  mounted() {
    cart().then((data) => {
      this.firstCate = data.list;
      this.changeIndex(0)
    });
  },
};
</script>

<style scoped>
.van-sidebar{
  width: 100%;
  display: flex;

}
.left{
  width: 15%;
}
.van-grid {
  flex: 1;
  text-align: center;
}
.van-empty{
  margin-left: 20%;
}
.van-grid-item{
  height: 100px;
}
</style>


